<template>
  <div class="hidden_over">
    <div class="file_flex">
      <el-checkbox-group
        v-model="checkList"
        class="file_flex"
        text-color="#000000"
        @change="option"
      >
        <div
          v-for="(item,index) in fileList"
          :key="index"
          class="showAnimate"
          @click="addToBar(item)"
        >
          <el-checkbox
            :label="item"
            class="checkBox"
            border
          >
            <file-card
              :id="item.ID"
              :file-name="item.name"
              :parent-id="item.parentId"
              :ext="item.ext?item.ext:''"
            />
          </el-checkbox>
        </div>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
import fileCard from "./fileCard.vue"
import PubSub from "pubsub-js"
// import {
//   fileList
// } from "@/assets/test"
import {
  getDirList, getDocList
} from "@/api/getDocList"
export default {
  name: "DiskFolder",
  components: {
    fileCard
  },
  data() {
    return {
      fileList: [],
      checkList: []
    }
  },
  watch: {
    "$route": {
      async handler(newValue) {
        PubSub.publish("optionCancel")
        PubSub.publish("optionCancelF")
        // this.fileList = fileList.filter(item=>item.parentId === newValue.params.parentId)
        console.log("执行中")
        const res1 = await getDirList(newValue.params.parentId)
        const res2 = await getDocList(newValue.params.parentId)
        console.log("目录", res1, "文件", res2)
        // this.fileList = [...res1.data.data, ...res2.data.data]
        // PubSub.publish("updateFileBar", newValue.params)
        if(res1.data.data.length !== 0 && res2.data.data.length !== 0) {
          this.fileList = [...res1.data.data, ...res2.data.data]
        } else if (res1.data.data.length !== 0 || res2.data.data.length !== 0) {
          this.fileList = res1.data.data.length !== 0 ? [...res1.data.data] : [...res2.data.data]
        } else {
          console.log("都是空")
          this.fileList = []
        }
        console.log(this.fileList)
      }
    }
  },
  // beforeUpdate() {
  //   console.log(this.fileList)
  // },
  // updated() {
  //   console.log(this.fileList)
  // },
  async mounted() {
    PubSub.subscribe("optionCancelF", ()=>{
      this.checkList = []
      PubSub.publish("optionCancel")
    })
    // this.fileList = fileList.filter(item=>item.parentId === this.$route.params.parentId)
    const res1 = await getDirList(this.$route.params.parentId)
    const res2 = await getDocList(this.$route.params.parentId)
    this.fileList = [...res1.data.data, ...res2.data.data]
  },

  methods: {
    addToBar(item) {
      PubSub.publish("updateFileList", item)
    },
    option(val) {
      console.log(val)
      PubSub.publish("optionShow", val)
    }
  }
}
</script>

<style lang="less" scoped>
.hidden_over{
  width:100%;
  height: 75vh;
  overflow: hidden;
  .file_flex{
      padding-bottom: 20px;
      width:76vw;
      height: 75vh;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-wrap: wrap;
      overflow: auto;
      .checkBox{
        margin:5px 10px 10px 5px;
        padding: 5px 10px 10px 5px;
        width:120px;
        height:150px;
        border-radius: 25px;
      }
  }
}
</style>
